import { Component, OnInit, ViewEncapsulation, ChangeDetectorRef } from '@angular/core';
import { ActivatedRoute, Router, NavigationEnd } from '@angular/router';

import { Observable, BehaviorSubject } from 'rxjs';
import { filter, map, startWith, debounceTime, takeUntil } from 'rxjs/operators';
import { FormControl } from '@angular/forms';
import { Subscription, Subject } from 'rxjs';

import { BatchService } from 'src/app/swagger/api/controllers/Batch';

import { ProgramSetupService } from 'src/app/modules/admin/program/program-setup/program-setup.service';

import { SessionService } from 'src/app/core/session/session.service';
import { SharedService } from 'src/app/shared/services/shared.service';
import { ModalsService } from 'src/app/modules/ui/modals/modals.service';

@Component({
  selector: 'app-mode-program-setup',
  templateUrl: './mode-program-setup.component.html',
  styleUrls: ['./mode-program-setup.component.scss'],
  encapsulation  : ViewEncapsulation.None
})
export class ModeProgramSetupComponent implements OnInit {

  // Private
  private _unsubscribeAll: Subject<any>;

  isDataLoaded:boolean = false;
  isError:boolean = false;

  modeData:string = "";

  _modesList:any;
  fullModesList:any = [];

  searchData:FormControl;

  search:string = '';
  deleteSubscribe:Subscription;

  ItemStartIndex: any = 0;
  ItemEndIndex: any;
  totalItems: any;
  itemLimit: any = 5;

  allData: any;

  message: any;

  constructor(
    private _changeDetectorRef: ChangeDetectorRef,
    private _activatedRoute: ActivatedRoute,
    private _router: Router,
    private batchService: BatchService,
    private programSetupService: ProgramSetupService,
    private sessionService:SessionService,
    private sharedService:SharedService,
    private modalsService: ModalsService
  ) {

    // Set the private defaults
    this._unsubscribeAll = new Subject();

    this.message = null;

    this._modesList = new BehaviorSubject(null);
    this.searchData = new FormControl('');

  }

  get modes$(): Observable<any>{
    return this._modesList.asObservable();
  }

  addModeProgram() {
    var id = this.sharedService.guid();
    this.programSetupService.setProgramSetupMatDrawer({id: id, category: 'mode', data: null, isedit: false})
  }

  editModeProgram(data){
    this.programSetupService.setProgramSetupMatDrawer({id: data.id, category: 'mode', data: data, isedit: true})
  }

  viewModeProgram(data){
    this.programSetupService.setProgramSetupMatDrawer({id: data.id, category: 'mode', data: data, isedit: false})
  }

  deleteModeProgram(id){
    this.modalsService.showDeleteConfirmModal(id)
  }

  getIndexParams(event){

    this.ItemStartIndex = event.ItemStartIndex;
    this.ItemEndIndex = event.ItemEndIndex;
    this.itemLimit = event.itemLimit;

  }

  onGlSearchFilter(event) {
		if (event != "") {
      let newData = this.allData.filter(item => {
        for (let field in item) {
          if (item[field] === null || item[field] === undefined) {
            continue;
          }
          if (item[field].toString().toLowerCase().indexOf(event.toString().toLowerCase()) !== -1) {
            return item;
          }
        }
      })

      this._modesList.next(newData);
		} else {
      this.fullModesList = this.allData;
      this._modesList.next(this.fullModesList);
    }

	}

  getModeList() {

    this.isDataLoaded = false;

    let params = {
      UserId: this.sessionService.userId,
      ClientId: this.sessionService.clientId
    }

    this.batchService.getBatchModeList(params).subscribe((res:any) => {

      this.isDataLoaded = true

      // Hide the message
      this.message = null;

      if(res.value) {

        let newData = res.value.filter(item => {
          return item.active
        });

        this.fullModesList = newData
        this.allData = newData
        this._modesList.next(newData);
      }
      else {

        this.isError = true;

        // Show the error message
        this.message = {
          appearance: 'outline',
          content   : res.errors,
          shake     : true,
          showIcon  : false,
          type      : 'error'
        };

      }

    }, error => {

      this.isDataLoaded = true
      this.isError = true;

      // Show the error message
      this.message = {
        appearance: 'outline',
        content   : 'Some error occurred',
        shake     : true,
        showIcon  : false,
        type      : 'error'
      };

    })

  }

  ngOnInit(): void {

    this.getModeList();

    this.programSetupService.programsetupentryrefreshcast.subscribe((res:any) => {
      if(res){
        this.getModeList();
      }
    })

    this.modes$.pipe( filter(res => res != null),
    ).subscribe((res:any) => {

      this.totalItems = res.length;
      this.ItemStartIndex = 0;

      if (this.totalItems > this.itemLimit) {
        this.ItemEndIndex = this.itemLimit;
      }
      else {
        this.ItemEndIndex = this.totalItems;
      }

    });

    // Subscribe to search input field value changes
    this.searchData.valueChanges
    .pipe(
      startWith(''),
      map((val) => {

        let newData = this.fullModesList.filter(item => {
          for (let field in item) {
            if (item[field] === null || item[field] === undefined) {
              continue;
            }
            if (item[field].toString().toLowerCase().indexOf(val.toString().toLowerCase()) !== -1) {
              return item;
            }
          }
        })

        this._modesList.next(newData);


      })
    )
    .subscribe();

    this.deleteSubscribe = this.modalsService.deleteindexcast.subscribe((id:any) => {


      if(id != null) {

        let details = {
          UserId: this.sessionService.userId,
          ClientId: this.sessionService.clientId,
          RoomId: id,
        }

        this.batchService.deleteBatchMode(details).subscribe((res:any) => {

          if(res.value) {
            this.fullModesList = this.fullModesList.filter(item => {
              return item.id != id
            })

            this.allData = this.fullModesList;
            this._modesList.next(this.fullModesList);
          }

        }, error => {

        })



      }

    })

  }

  ngOnDestroy(): void
    {
        // Unsubscribe from all subscriptions
        this._unsubscribeAll.next();
        this._unsubscribeAll.complete();
        this.deleteSubscribe.unsubscribe();
    }

}
